.signup-navbar {
  background-color: #371838cc;
  padding: 0 0;
  .Logo {
    width: 9rem;
  }
}
.signup-container {
  background: #9d2f760f;
  font-family: sans-serif;
  font-size: 10px;
  width: 100%;
  height: 100vh;

  .form-container {
    background: #fff;
    padding: 3em 4em 1em;
    width: 465px;
    margin: 1rem auto 0;
    box-shadow: 0 0 1em #222;
    border-radius: 2px;

    .form-label {
      font-size: 13px;
    }

    .form-control {
      display: block;
      box-sizing: border-box;
      width: 100%;
      outline: none;
      margin: 0;
    }
    .form-group {
      margin-bottom: 2rem;
    }

    h2 {
      margin: 0 0 1.5rem 0;
      text-align: center;
      font-size: 37px;
    }
    .btn {
      padding: 1rem;
    }
    .already-member {
      text-align: center;

      a {
        background: none;
        border: none;
        padding: 0;
        color: #626365;
        cursor: pointer;
        padding-top: 1rem;

        &:hover {
          color: #1c1a1a;
        }
        &:active {
          color: #d0b7b7;
          border: none;
          background-color: #ff000000;
          box-shadow: none !important;
        }
        &:focus {
          box-shadow: none;
        }
      }
    }
  }
}
