.get-quote-container {
  width: 100%;
  position: relative;
  text-align: center;
  img {
    //filter: blur(1px);
  }
  .get-quote-content {
    position: absolute;
    top: 20%;

    .get-quote-header {
      h2 {
        color: #eef5f1;
        display: inline-block;
        font-size: 3rem;
        font-weight: 600;
        width: 100%;
        z-index: 100;
        padding-bottom: 1rem;
      }
    }

    .get-quote-button {
      width: 100%;
      display: inline-block;
      padding-top: 4rem;
    }
  }
}

@media screen and (max-width: 920px) {
  .get-quote-container {
    .get-quote-content {
      .get-quote-header {
        h2 {
          font-size: 2rem;
          padding-bottom: 1rem;
        }
      }

      .get-quote-button {
        padding-top: 4rem;
      }
    }
  }
}

@media screen and (max-width: 630px) {
  .get-quote-container {
    .get-quote-content {
      .get-quote-header {
        h2 {
          font-size: 1.5rem;
          padding-bottom: 1rem;
        }
      }

      .get-quote-button {
        padding-top: 3rem;
      }
    }
  }
}

@media screen and (max-width: 470px) {
  .get-quote-container {
    .get-quote-content {
      .get-quote-header {
        h2 {
          font-size: 1rem;
          padding-bottom: 1rem;
        }
      }

      .get-quote-button {
        width: 100%;
        display: inline-block;
        padding-top: 2rem;
      }
    }
  }
}
